body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
  background-color: #e8ecf4;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.wrapper {
  display: flex;  
  flex-flow: row wrap;
  font-weight: bold;
  text-align: left;
}

/* We tell all items to be 100% width, via flex-basis */
.wrapper > * {
  padding: 10px;
  flex: 1 1 100%;
}

/* Small screens */
@media all and (max-width: 600px) {
  /* We tell the list share a row */
  .line-item { flex: 1 1 auto; }
  .main-content { 
    min-height: 100vh
  }
}

/* Medium and Large screens */
@media all and (min-width: 600px) {
  .footer    { order: 1; }
  .main-content { 
    order: 2; 
  }
  .dashboard {
    order: 2; 
  }
}
.header {
    display: flex;
    justify-content: space-between;
    background: saddlebrown;
    color: white;
    padding: 15px;
    font-size: 20px;
}


.main-text {
    text-align: center;
}
.main-text span {
    margin-left: 5px
}
.actions-btn {
    margin-right: 20px;
}
select {
    margin: 5px;
    background: none;
    font-size: 15px;
    font-weight: 500;
    color: white;
}

/* Small screens */
@media all and (max-width: 450px) {
    .actions-btn {
        text-align: right;
    }
}

/* Small screens */
@media all and (max-width: 450px) {
    .actions-btn {
        text-align: right;
    }
}

/* Large screens */
@media all and (min-width: 800px) {
    .actions-btn {
        text-align: left;
    }
}


.main-content {
    display: flex;
    flex-flow: row wrap;
    align-content: baseline;
}
.line-item {
    background: #a07854;
    font-family: inherit;
    border-radius: 5px;
    padding: 5px;
    width: 200px;
    height: 50px;
    margin: 10px;
    color: white;
    font-size: 0.8em;
    text-align: left;
}

.line-item span {
    margin-left: 5px
}

/* Small screens */
@media all and (max-width: 450px) {
    .line-item {
        line-height: 45px;
    }

    .main-text {
        text-align: left;
        font-size: 15px;
        width: 180px;
        line-height: 50px;
    }
}

/* Mediam screens */
@media all and (min-width: 450px) and (max-width: 800px) {
    .line-item {
        line-height: normal;
    }
}



